@import '~@material/react-text-field/dist/text-field.css';

body {
    background-color: #f5f5f5;
    margin: 0;
}

.container {
    padding-top: 7%;
}

.content, .login-component {
    min-width: 30%;
    max-width: 62.1%;
    margin: auto;
    background-color: #ffffff;
    border: 1px solid #e0e0e0;
    box-shadow: 3px 1px 7px #9e9e9e;
    padding: 50px;
}

.content-input, .content-input:focus {
    width: 100%;
    height: 100vh;
    font-size: 20px;
    resize: none;
    outline: none;
    /*overflow: hidden;*/
    border-style: hidden;
}

.content > i {
    margin-bottom: 30px;
}

.login-component {
    padding: 100px 0 70px;
    max-width: 500px;
    text-align: center;
}

.login-component > i {
    color: #6200ee;
    font-size: 70px;
    display: block;
    margin-bottom: 50px;
}

.login-component > .mdc-text-field {
    display: block;
    margin: auto auto 17px;
    width: 70%;
}

.login-component > button {
    margin-top: 34px;
}

.header-username {
    width: 100%;
    margin-right: 24px;
    text-align: right;
    font-family: 'Roboto', sans-serif;
}

